/* src/main/frontend/src/pages/HomePage.css */

.home-page {
    display: flex;
    justify-content: left; /* 왼쪽 정렬 */
    align-items: center; /* 세로 중앙 정렬 */
    height: 100vh; /* 전체 화면 높이 */
    padding-left: 50px; /* 좌측 패딩 */
}

.login-form {
    display: flex;
    flex-direction: column; /* 요소들을 수직으로 배치 */
    width: 300px; /* 폼 너비 */
    gap: 10px; /* 입력 필드 간격 */
}

.login-form input,
.login-form button {
    padding: 10px; /* 입력 필드와 버튼 패딩 */
    font-size: 16px; /* 글자 크기 */
}

.login-form button {
    background-color: #007BFF; /* 버튼 배경색 */
    color: white; /* 버튼 글자색 */
    border: none; /* 버튼 테두리 제거 */
    cursor: pointer; /* 포인터 모양 변경 */
}
